import React from 'react';
import injectSheet from 'react-jss';

import TopSection from '../components/TopSection';
import Link from '../components/Link';

const maxWidth = 1130;
const topSectionHeight = '39rem';

const styles = {
    section: {
        maxWidth: maxWidth,
        margin: '0 auto',
        padding: '2rem 0'
    }
};

export default injectSheet(styles)(({ classes, data }) => {
    return (
        <div>
            <TopSection
                maxWidth={maxWidth}
                height={topSectionHeight}
                margin="0 auto"
                sizesBackground={data.imageBackground.childImageSharp.sizes}
                justifyContent='center'>
                <h1>{data.contents.childHomeToml.topSection.title}</h1>
                <p>{data.contents.childHomeToml.topSection.description}</p>
                <Link to="/downloads">
                    <button type="button">
                        {data.contents.childHomeToml.topSection.download}
                    </button>
                </Link>
            </TopSection>
            <section className={classes.section}>
                features
            </section>
        </div>
    );
});

export const query = graphql`
    query IndexHomeQuery {
        ...IndexHomeFragment
    }

    fragment IndexHomeFragment on RootQueryType {
        imageBackground: file(relativePath: { eq: "bg_home.jpg" }) {
            childImageSharp {
                sizes(maxWidth: 1130) {
                    ...GatsbyImageSharpSizes
                }
            }
        }

        contents: file(relativePath: { eq: "home.toml" }) {
            childHomeToml {
                topSection {
                    title
                    description
                    download
                }
            }
        }
    }
`;



