import React from 'react';
import Img from 'gatsby-image';
import injectSheet from 'react-jss';

const TopSection = ({ classes, children, sizesBackground }) => {
    return (
        <div className={classes.container}>
            <section className={classes.section}>
                <div className={`${classes.contents} top-section`}>
                    {children}
                </div>
            </section>
            <Img
                className={classes.background}
                sizes={sizesBackground}
                />
        </div>
    );
};

const styles = {
    container: {
        width: '100%',
        height: (props) => props.height
    },
    section: {
        position: 'absolute',
        width: '100%',
        zIndex: 1
    },
    contents: {
        maxWidth: (props) => props.maxWidth,
        height: (props) => props.height,
        margin: (props) => props.margin,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: (props) => props.justifyContent
    },
    background: {
        height: (props) => props.height,
        overflow: 'hidden'
    }
};

export default injectSheet(styles)(TopSection);



